.shimmer {
  background: linear-gradient(
    90deg,
    transparent 25%,
    #e8e8e8 50%,
    transparent 75%
  );
  background-size: 200% 100%;
  animation: shimmer 1.5s infinite;
}

@keyframes shimmer {
  0% {
    background-position: 0 200%;
  }
  100% {
    background-position: -200% 0;
  }
}
